.navigation {
  background-color: rgba(0, 0, 0, 0.5);
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  box-shadow: 0px 8px 10px -5px rgba(0, 0, 0, 0.2),
    0px 16px 24px 2px rgba(0, 0, 0, 0.14), 0px 6px 30px 5px rgba(0, 0, 0, 0.12);
  opacity: 1;
  left: auto;
  transform: translateX(225px);
  transition: transform 250ms ease-in-out, opacity 250ms ease-in-out;
  .hamburger {
    position: absolute;
    left: -64px;
    top: 0;
    opacity: 1;
    &.active {
      width: 64px;
      height: 64px;
      border: none;
      display: flex;
      padding: 6px 8px;
      cursor: pointer;
      border-radius: 5px;
      align-items: center;
      justify-content: center;
      background-color: inherit;
      transition: background-color, 250ms;
      background-color: transparent;
      &:hover {
        text-decoration: none;
        background-color: rgba(18, 18, 18, 0.04);
      }
      svg {
        font-size: 28px;
      }
    }
    &.dark {
      color: white;
          &:hover {
            background-color: rgba(255, 255, 255, 0.08);
          }
    }
    display: none;
  }
  nav {
    float: right;
    background-color: white;
    width: 225px;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    overflow: scroll;
    &::-webkit-scrollbar {
      width: 2px;
      height: 2px;
    }
    &::-webkit-scrollbar-thumb {
      background-color: rgba(58, 55, 55, 0.8);
    }
    &::-webkit-scrollbar-track {
      background: transparent;
    }
    &.dark {
      background-color: #111;
      a {
        &:hover {
          background-color: rgba(255, 255, 255, 0.08);
        }
        &.active {
          color: white;
        }
      }
    }
    .close {
      color: rgba(0, 0, 0, 0.54);
      position: absolute;
      top: 20px;
      right: 20px;
      background-color: inherit;
      border: 0;
      font-size: 24px;
      width: 44px;
      height: 44px;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
      &:hover {
        background-color: rgba(0, 0, 0, 0.04);
      }
      &.dark {
        svg {
          color: white;
        }
        &:hover {
          background-color: rgba(255, 255, 255, 0.08);
        }
      }
    }
    .logo {
      display: none;
      cursor: pointer;
    }
    .links {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      width: 100%;
      transition: transform 250ms ease-in-out;
      .navigationLinks {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        gap: 50px;
        margin-bottom: 50px;
      }
      div {
        width: 100%;
      }
    }
  }
  &.active {
    opacity: 1;
    left: -225px;
    transform: translateX(0px);
    z-index: 2;
  }
}

@media screen and (min-width: 900px) {
  .navigation {
    position: relative;
    transition: 0s all;
    border-right: 1px solid rgba(0, 0, 0, 0.12);
    box-shadow: none;
    opacity: 1;
    transform: none;
    &.active {
      transform: none;
      opacity: 1;
      left: auto;
    }
    nav {
      width: 255px;
      height: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      overflow: hidden;

      .close {
        display: none;
      }
      .logo {
        display: inline-block;
        position: absolute;
        top: 80px;
        left: 60px;
      }
      .links {
        height: 100%;
        padding-top: 200px;
        padding-bottom: 55px;
        justify-content: space-between;
      }
    }
  }
  .hamburger {
    display: none;
    &.active {
      display: none;
    }
  }
}
